import { getCredentials } from './auth'
import createEventLog from './create-event-log'
import createPageView from './create-page-view-log'
import Analytics from 'analytics'
import awsPinpointPlugin from '@analytics/aws-pinpoint'

export default class Amazon {
  constructor (options) {
    const { app = {}, fips, userPoolID } = options
    this.analytics = Analytics({
      app: app.name,
      plugins: [
        awsPinpointPlugin({
          fips,
          pinpointAppId: app.id,
          getCredentials: () => {
            return getCredentials(userPoolID, { fips })
          }
        })
      ]
    })

    this.name = 'amazon'
    Object.assign(this, options)
  }

  logPageView (page, options) {
    const telemetryPayload = createPageView({
      page,
      previousPage: this.previousPage,
      options,
      dimensionLookup: this.dimensions,
      metricLookup: this.metrics
    })
    this.previousPage = telemetryPayload.attributes
    this.analytics.track('pageView', telemetryPayload)
  }

  logEvent (event = {}) {
    const telemetryPayload = createEventLog({
      event,
      dimensionLookup: this.dimensions,
      metricLookup: this.metrics
    })
    const { name } = telemetryPayload
    this.analytics.track(name, telemetryPayload)
  }
}
