import mapCustomMetricsAndDimensions from './map-metrics-and-dimensions'
import dynamicImport from '../helpers/dynamic-import'

const MODULE_SPECIFIER = 'universal-analytics'
export default class Google {
  constructor(options = {}) {
    this.name = 'google'
    Object.assign(this, options)
  }

  static async load(options) {
    let ua
    if (typeof window === 'undefined') {
      try {
        ua = await dynamicImport(MODULE_SPECIFIER)
      } catch (error) {
        throw new Error(`Failed to import ${MODULE_SPECIFIER}. ${error.message}`)
      }
      options.serverTracker = getServerSideTrackingFunction(ua.default, options.trackingId)
    }

    return new Google(options)
  }

  logPageView (page, options) {
    const pageviewObj = buildPageViewObject(page, options, this.dimensions, this.metrics)
    getBrowserTrackingFunctions((browserTrackers) => {
      browserTrackers.forEach((browserTracker) => {
        browserTracker.send(pageviewObj)
      })
    })
  }

  logEvent (event) {
    const eventObject = buildEventObject(event, this.dimensions, this.metrics)

    // If server-side, execute universal analytics and exit
    if (this.serverTracker) {
      return this.serverTracker.event(eventObject).send()
    }

    getBrowserTrackingFunctions(browserTrackers => {
      browserTrackers.forEach(browserTracker => {
        browserTracker.send(eventObject)
      })
    })
  }
}

function getServerSideTrackingFunction (fn, trackingId) {
  if (typeof trackingId === 'undefined') {
    throw new TypeError('Google Analytics Tracking ID not provided')
  }
  return fn(trackingId)
}

function getBrowserTrackingFunctions (callback) {
  if (window.ga) {
    window.ga(() => {
      callback(window.ga.getAll())
    })
  } else {
      console.log(new Error('Google Analytics trackers not available'))
  }
}
function buildPageViewObject (page, options, dimensions = {}, metrics = {}) {
  const standardTelemetry = {
    hitType: 'pageview',
    page: page || window.location.pathname
  }

  const mappedCustomTelemetryData = mapCustomMetricsAndDimensions({
    customTelemetryData: options,
    dimensionLookup: dimensions,
    metricLookup: metrics
  })

  return Object.assign({}, standardTelemetry, mappedCustomTelemetryData)
}

function buildEventObject (event, dimensions = {}, metrics = {}) {
  const standardTelemetryData = {
    hitType: 'event',
    eventCategory: event.category || 'none',
    eventAction: event.action,
    eventLabel: event.label
  }

  const mappedCustomTelemetryData = mapCustomMetricsAndDimensions({
    customTelemetryData: event,
    dimensionLookup: dimensions,
    metricLookup: metrics })
  return Object.assign({}, standardTelemetryData, mappedCustomTelemetryData)
}
