import formatTelemetryAttributes from './format-telemetry-attributes'
import formatTelemetryMetrics from './format-telemetry-metrics'

export default function createEventLog ({
  event = {},
  dimensionLookup = {},
  metricLookup = {}
}) {
  const { eventType = 'other' } = event
  const metrics = formatTelemetryMetrics(event, metricLookup)
  const eventAttributes = formatTelemetryAttributes({
    telemetryData: { eventType, ...event },
    dimensionLookup,
    excludeKeys: ['workflow', ...Object.keys(metrics), ...Object.keys(metricLookup)]
  })

  return {
    name: eventType,
    referrer: document.referrer,
    hostname: window.location.hostname,
    path: window.location.pathname,
    ...eventAttributes,
    ...metrics
  }
}
